.profile__info{
  display: inline-grid;
  grid-template-columns: 294px 30px;
  grid-template-rows: auto;
  gap: 0 18px;
  margin: 0;
  margin-left: 30px;
  padding-top: 22px;
  padding-bottom: 25px;
}

@media screen and (min-width: 618px) and (max-width: 767px) {
  .profile__info {
    grid-template-columns: 235px 47px;
    padding-bottom: 36px;
    margin-left: 30px;
    gap: 0 10px;
  }
}

@media screen and (min-width: 320px) and (max-width: 618px) {
    .profile__info {
      grid-template-columns: 235px 47px;
      padding-bottom: 36px;
      margin-left: 0;
      gap: 0 10px;
    }
  }