.popup {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.5);
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.7s, opacity 0.7s linear;
    position: fixed;
    top: 0px;
    left: 0px;
    padding: 0;
    margin: 0;
}