.form__input{
    outline: none;
    border: none;
    border-bottom: 1px solid rgba(0,0,0,0.2);
    width: 358px;
    margin-left: 36px;
    padding: 0;
    min-height: 27px;
    font-family: 'Inter', Arial, sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #000;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
    .form__input{
      width: 238px;
    }
  }
