.profile{
    margin: 0 auto;
    padding: 0;
    display: flex;
}
  
@media screen and (min-width: 619px) and (max-width: 880px) {
  .profile {
    align-items: center;
    padding: 0 10px;
  }
}

  @media screen and (min-width: 320px) and (max-width: 618px) {
    .profile {
      align-items: center;
      flex-direction: column;
      padding: 0 19px;
    }
  }